body {
  height: 100%;
  background-color: #14ff14b3;
  font-family: Pacifico, sans-serif;
  font-size: 18px;
}

header {
  border-bottom: 2px solid green;
  align-items: center;
  gap: 100px;
  padding: 20px 50px;
  display: flex;
}

.nav {
  gap: 30px;
  display: flex;
}

.nav__link {
  color: bisque;
  transition: transform .5s linear, color .5s linear;
}

.nav__link:hover {
  color: #014111;
  transform: scale(1.5);
}

.logo {
  color: bisque;
  font-family: Cambria, serif;
  font-size: 30px;
  font-weight: 700;
  transition: transform .5s linear, color .5s linear;
}

.logo:hover {
  color: #014111;
  transform: scale(1.5);
}

section {
  height: 100%;
  text-align: center;
  margin: 0 auto;
  padding: 50px;
}

footer {
  padding: 50px;
}

li {
  margin: 0;
  list-style: none;
}

a {
  font-family: Pacifico, sans-serif;
  font-size: 18px;
  text-decoration: none;
  display: block;
}

img {
  display: block;
}

h1, h2, p {
  margin: 0;
  padding: 0;
}

